<template>
  <!-- 添加 -->
  <div>
    <el-dialog title="添加用户" :visible="visible" :before-close="handleClose">
      <el-form :model="form" class="demo-form-inline" label-width="90px" :rules="rules">
        <el-row :gutter="gutter">
          <el-col :span="11">
            <el-form-item label="昵称">
              <el-input v-model="form.nickname"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="是否激活">
              <el-select v-model="form.isactive" placeholder="是否激活">
                <el-option label="激活" value="1"></el-option>
                <el-option label="未激活" value="0"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="gutter">
          <el-col :span="11">
            <el-form-item label="账号" prop="username">
              <el-input v-model="form.username"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="密码">
              <el-input v-model="form.password"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="gutter">
          <el-col :span="11">
            <el-form-item label="电话">
              <el-input v-model="form.phone"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="邮箱">
              <el-input v-model="form.email"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="gutter">          
          <el-col :span="11">
            <el-form-item label="有效期">
              <el-date-picker v-model="form.expirydate" type="date" placeholder="请选择日期" format="yyyy-MM-dd"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose">取 消</el-button>
        <el-button type="primary" @click="handleSave">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { addUser } from "@/api/usermanage";
export default {
  name: "",
  components: {},
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      form: {
        username: "",
        password: "",
        nickname: "",
        profile: "",
        phone: "",
        email: "",
        age: "",
        isactive: "1",
        expirydate: ""
      },
      gutter: 100, //表单间距
      rules: {
        //校验
        username: [{ required: true, message: "请输入用户名", trigger: "blur" }]
      }
    };
  },
  watch: {},
  computed: {},
  methods: {
    handleSave() {
      //保存
      addUser(this.form)
        .then(response => {
          if (response.code == 200) {
            this.$message({
              message: "添加成功",
              type: "success"
            });            
          } else {
            this.$message({
              message: "添加失败",
              type: "error"
            });
            console.log(response.message);
          }
          this.$emit("close", {close:false,query: true});
        })
    },
    handleClose(done) {
      //取消
      this.$confirm("确认关闭？").then(_ => {
          done;          
        }).catch(_ => {
          console.log(_);
        }).finally(_ => {
          this.$emit("close", {close:false,query: false});
        })
    }
  }
};
</script>
<style lang="scss" scoped>
</style>